export default {
	data() {
		return {
			distance: -740,
			skimList: [
			],
			notice: [
			],
			noWinner: true
		};
	},
	onLoad() {
		let uuid = ''
		try{
			uuid = uni.getStorageSync('uuid')
		}catch(e){
			//TODO handle the exception
		}
		if (!uuid) {
			uni.navigateTo({
				url: '../login/login'
			})
		} else {
			this.getSkims()
			this.getNotice()
		}
	},
	onPullDownRefresh() {
		this.getSkims();
	},
	methods: {
		goPage(id) {
			uni.navigateTo({
				url: './draw-detail?data=' + JSON.stringify(this.skimList[id])
			})
		},
		headerRoll() {
			if (this.distance< -400) {
				this.distance = 710
			}
			this.distance = this.distance - 3
			setTimeout(()=>{
				this.headerRoll()
			},40)
		},
		getNotice() {
			this.doRequest('/winner/get').then(res=>{
				if (res.data.length >1) {
					this.notice = res.data
					this.noWinner = false
					this.scroll()
				} else if (res.data.length == 0) {
					this.noWinner = true
					this.headerRoll()
				}
			})
		},
 		scroll(){
		    setTimeout(()=>{
			    this.notice.push(this.notice[0]);
			    this.notice.shift();
				this.scroll()
		    },2000)
		},
		getSkims() {
			this.doRequest('/skim/getSkims').then(res=>{
				console.log(res);
				let data = res.data.map(i=>{
					i.closeDate = this.formatdate(i.closeDate)
					return i
				})
				this.skimList = data
			})
		},
		formatdate(a) {
			let date = new Date(a);
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
			let h = date.getHours();
			let m = date.getMinutes()
			let s = date.getSeconds()
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			h = h > 9 ? h : '0' + h;
			m = m > 9 ? m : '0' + m;
			s = s > 9 ? s : '0' + s;
			return `${year}-${month}-${day} ${h}:${m}:${s}`;
		}
		
		
	},
	onShareAppMessage(res) {
		uni.showToast({
			title: 'share'
		})
	    return {
	      title: '王者荣耀，免费抽皮肤，和好兄弟开黑...',
	      path: '/pages/tabbar/login/login?parentID='+ this.$store.state.uuid
	    }
	}
};
